* {

    font-family: 'Urbanist', sans-serif;
}


body{
    overflow-x: hidden;
}

p {
    margin-bottom: 0;
}

h1,h2,h3,h4,h5,h6{
    margin-bottom:0;
}




.first {
    background-color: #ff385c;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    transition: all 0.3s ease-in-out;
    min-height: 70px;
    width:50vw;
    font-weight: 500;
}

.third {

    display: flex;
    align-items: center;
    justify-content: center;
    color: black;
    width: 100vw;
    transition: all 0.3s ease-in-out;
    min-height: 70px;
    background-image: url("https://content.asos-media.com/-/media/homepages/unisex/homepages/2021/10-oct/cbs-creative-pack/gradient/4_dt_2880x850px.jpg");
    background-position: center;
    background-size: cover;
    min-height: 150px;
    font-size: 1.4rem;
    font-weight: 900;
    line-height:1.2
}

.forth{
    display: flex;
    align-items: center;
    justify-content: center;
    color: black;
    width: 100vw;
    transition: all 0.3s ease-in-out;
    min-height: 70px;
    background-color: #26E066;
   margin-bottom: 30px;
    background-position: center;
    background-size: cover;
    min-height: 180px;
    font-size: 2rem;
    font-weight: 900;
    line-height: 1.3;
   
}



.second {
    background-color: black;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    min-height: 70px;
    width:50vw;
    font-weight: 500;
}

.offer {
    margin-top: 10px;

    text-align: center;
    display: flex;
    justify-content: center
}

@keyframes slide-left {
    from {
        -webkit-transform: translateX(0);
        transform: translateX(0);
    }

    to {
        -webkit-transform: translateX(-650%);
        transform: translateX(-650%);
    }
}

.aounce-text {
    display: flex;
    position: relative;
    flex-shrink: 0;
    animation: slide-left 20s linear infinite;



}

@media only screen and (max-width: 776px){
.first,.second{
    display: none;

    
}

.offer {
    margin-top: 0px;
    text-align: center;
    display: flex;
    justify-content: center;
  }

  
}

.brfilter{
display: none;
}

@media only screen and (max-width: 550px){
    .third {

        display: flex;
        align-items: center;
        justify-content: center;
        color: black;
        width: 100vw;
        transition: all 0.3s ease-in-out;
        min-height: 70px;
        background-image: url("https://content.asos-media.com/-/media/homepages/unisex/homepages/2021/10-oct/cbs-creative-pack/gradient/4_dt_2880x850px.jpg");
        background-position: center;
        background-size: cover;
        min-height: 150px;
        font-size: 1.3rem;
        font-weight: 900;
        line-height:1.2
    }

   .brfilter{
        display:block;
    }

    .forth{
        display: flex;
        align-items: center;
        justify-content: center;
        color: black;
        width: 100vw;
        transition: all 0.3s ease-in-out;
        min-height: 70px;
        background-color: #26E066;
       margin-bottom: 30px;
        background-position: center;
        background-size: cover;
        min-height: 180px;
        font-size: 1.3rem;
        font-weight: 900;
        line-height: 1.3;
       
    }

    @keyframes slide-left {
        from {
            -webkit-transform: translateX(0);
            transform: translateX(0);
        }
    
        to {
            -webkit-transform: translateX(-150%);
            transform: translateX(-150%);
        }
    }

    .title{
        font-size: 12px;
        color: white
    }
    .aounce-text {
        display: flex;
        position: relative;
        flex-shrink: 0;
        animation: slide-left 10s linear infinite;
    
    
    
    }
      
    }

.first p ,.second p, .third p{
    transition: opacity 0.3s ease-in-out;
}

.first:hover p,
.second:hover p,
.third:hover p {
    opacity: 0.6;

}


.subt{
    font-size: 1.2rem;
}

.subt-t{
    font-size: 0.8rem;
    font-weight: 500;
}